@font-face {
    font-family: "Golos-Text_Regular";
    src: local("Golos-Text_Regular"),
        local("Golos-Text_Regular"),
        url("./fonts/Golos-Text_Regular.woff2") format("woff2");
    font-weight: normal;
}

@keyframes fadeIn {
    0% {
        opacity: 0.2;
    }

    100% {
        opacity: 1;
    }
}

* {
    box-sizing: border-box;
    font-family: "Golos-Text_Regular", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    transition: background-color 0.1s ease-in-out, color 0.1s ease-in-out, opacity 0.1s ease-in-out, height 0.1s ease-in-out;
}

:root {
    --akcent: #3A529B;
    --akcent-two: #4b69c4;
    --akcent-disabled: #556491;
    --bg: #f2f4f8;
    --bg-pan: #fff;
    --sdw: 0 4px 10px 0 #3A529B20;
    --red: crimson;
}

html,
body {
    margin: 0px;
    background-color: var(--bg);
    font-size: 14px;
}

main {
    padding: 10px;
    min-height: 80vh;
    width: 100%;
    max-width: 1900px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

main * {
    animation: fadeIn 0.3s ease-in-out forwards;
}

a {
    text-decoration: none;
    color: var(--akcent);
}


.panel {
    background-color: var(--bg-pan);
    padding: 10px;
    box-shadow: var(--sdw);
    border-radius: 4px;
    gap: 10px;
}

.mini {
    font-size: 0.8rem;
    color: #8d8d8d;
    margin: 2px 0px;
}

.clm {
    display: flex;
    flex-direction: column;
}

.row {
    display: flex;
    flex-direction: row;
}

.gap {
    gap: 10px;
}

.gapmin {
    gap: 5px;
}

.grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
    gap: 10px;
}

.grid .panel {
    flex-basis: 400px;
    flex-grow: 1;
    min-height: 10vh;
    display: flex;
    justify-content: space-between;
}

.row.ctr {
    align-items: center;
}

.clm.ctr {
    justify-content: center;
}

.big {
    font-size: 5.0rem;
    color: var(--akcent);
}

.centered {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.right {
    text-align: right;
}

.rightf {
    display: flex;
    flex-direction: row;
    justify-content: end;
}

.nomarg {
    margin: 0px;
}

.btw {
    justify-content: space-between;
}

input,
select,
textarea {
    background-color: var(--bg);
    padding: 6px 8px;
    border: 2px solid var(--bg);
    border-radius: 4px;
    font-size: 0.9rem;
}

button {
    background-color: var(--akcent);
    padding: 6px 8px;
    border: 2px solid var(--akcent);
    color: var(--bg-pan);
    border-radius: 4px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-size: 0.9rem;
}

button.red {
    background-color: var(--red);
    border: 2px solid var(--red);
}

.end {
    display: flex;
    justify-content: end;
}

.searchbox div.clm {
    flex-grow: 1;
    flex-basis: 300px;
}

.searchbox div.clm input,
.searchbox div.clm textarea {
    width: 100%;
}

.searchbox div.clm textarea {
    resize: none;
}

.searchbox div.clm p.mini {
    margin-left: 5px;
}

label.mini.row.ctr {
    text-transform: uppercase;
}

.pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.outpanel {
    border: #c0c0c0 2px solid;
    font-weight: 700;
    color: #c0c0c0;
    display: flex;
    padding: 8px;
    border-radius: 8px;
    font-size: 0.8rem;
}

.searchitem {
    display: flex;
    justify-content: space-between;
}

p.max4 {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.searchbox {
    z-index: 300;
}

.archive {
    background-color: gold;
    padding: 4px;
    font-size: 0.7rem;
}

.algctr {
    align-items: center;
}

pre {
    white-space: pre-wrap;
}

.max60 {
    flex-grow: 3;
    flex-basis: 60%;
}

.rightBar {
    flex-grow: 1;
    flex-basis: 20%;
    position: sticky;
    top: 65px;
    right: 0px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.structureNotes {
    background-color: var(--bg);
    color: var(--akcent-disabled);
    padding: 4px;
    position: relative;
    transition: all 0.3s ease;
    line-height: 1.5;
}

.structureNotes.hide {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    height: fit-content;
    max-height: 75px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 70%, rgba(0, 0, 0, 0));
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1) 70%, rgba(0, 0, 0, 0));
}

@keyframes placeHolderShimmer {
    0% {
        background-position: -490px 0
    }

    100% {
        background-position: 490px 0
    }
}

.loading {
    color: transparent;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeHolderShimmer;
    animation-timing-function: linear;
    background: #f2f4f8;
    background-image: -webkit-gradient(linear, left center, right center, from(#f2f4f8), color-stop(.2, #f6f8fd), color-stop(.4, #f2f4f8), to(#f2f4f8));
    background-image: -webkit-linear-gradient(left, #f2f4f8 0%, #f6f8fd 20%, #f2f4f8 40%, #f2f4f8 100%);
    background-size: 980px 898px;
    position: relative;
    border-radius: 4px;
    border: 2px solid transparent;
    transform: none;
}

.loading:hover,
.loading:active,
.loading:focus {
    border: 2px solid transparent;
    transform: none;
}